import * as React from "react";

import Navigation from "../Components/Navbar.js";
//import Footer from "../Components/Footer.js";
import HighlightCard from "../Components/HighlightCard.js";
import Login from "../images/Login.png";
import ML from "../images/ML.png";
import Website from "../images/Website.png";

import Container from "react-bootstrap/Container";
import { SummaryAnimation } from "../Animations.js";

import { library } from "@fortawesome/fontawesome-svg-core";
import {
  faJava,
  faPython,
  faGithub,
  faHtml5,
  faReact,
  faGit,
  faJs,
  faCss3,
} from "@fortawesome/free-brands-svg-icons";

library.add(faJava, faPython, faGithub, faHtml5, faReact);

const projects = () => {
  return (
    <main>
      <Container fluid>
        <Navigation />
      </Container>

      <Container>
        <SummaryAnimation>
          <h2>Projects</h2>
          <div>
            Here's some stuff that I've done, check out my{" "}
            <a
              href={"https://github.com/emeryli"}
              target="_blank"
              rel="noreferrer"
            >
              Github
            </a>{" "}
            for more!
          </div>
          <br />
          <HighlightCard
            title={"Academic Prediction Using Machine Learning"}
            bodyText={
              "A machine learning model, developed from scratch using CVS, that transforms your input data into precise predictions of your math scores, helping you achieve your academic goals!"
            }
            image={ML}
            icons={[faPython]}
            repo={"MachineLearningProject"}
          />
          <br />
          <HighlightCard
            title={"Web Application"}
            bodyText={
                "The web application utilizes Django RESTful APIs to facilitate interactions between the front and back ends, enabling login authentication and integrating the backend prediction model through a React interface."            
            }
            image={Login}
            icons={[faReact]}
            repo={"WebApplication"}
          />
          <br />
          <HighlightCard
            title={"Personal Website"}
            bodyText={
              "Personal portfolio website with info about me, some of my projects, and more! Developed in part to get more comfortable with React.js and front-end development as a whole."
            }
            image={Website}
            icons={[faReact]}
            repo={""}
          />
        </SummaryAnimation>
      </Container>
      <br />
      
    </main>
  );
};

export default projects;